"use strict";
const $ = require('jquery');
var Settings = require('./../constraints/settings.json');
const flag_actions_1 = require('./../actions/flag.actions');
let FlagSource = {
    fetchFlags() {
        return {
            remote(state) {
                return new Promise((resolve, reject) => {
                    $.ajax({
                        url: Settings.uBaseName + Settings.uServer + "flags.php",
                        data: {},
                        success: function (response) {
                            resolve($.parseJSON(response));
                        },
                        error: function (response) {
                            console.log(response);
                            reject(response);
                        }
                    });
                });
            },
            local(state) {
                //TODO : Figure out why local doesn't work =(
                return null;
            },
            success: flag_actions_1.flagActions.fetchFlags,
            error: flag_actions_1.flagActions.failed,
            loading: flag_actions_1.flagActions.loading,
            shouldFetch: () => true
        };
    }
};
exports.flagSource = FlagSource;
