"use strict";
const $ = require('jquery');
var Settings = require('./../constraints/settings.json');
const food_actions_1 = require('./../actions/food.actions');
let FoodSource = {
    fetchFoods() {
        return {
            remote(state) {
                return new Promise((resolve, reject) => {
                    $.ajax({
                        url: Settings.uBaseName + Settings.uServer + "foods.php",
                        data: {},
                        success: function (response) {
                            resolve($.parseJSON(response));
                        },
                        error: function (response) {
                            console.log(response);
                            reject(response);
                        }
                    });
                });
            },
            local(state) {
                //TODO : Figure out why local doesn't work =(
                return null;
            },
            success: food_actions_1.foodActions.fetchFoods,
            error: food_actions_1.foodActions.failed,
            loading: food_actions_1.foodActions.loading,
            shouldFetch: () => true
        };
    }
};
exports.foodSource = FoodSource;
//# sourceMappingURL=food.source.js.map


